import { Sprite, SpriteAtlas, UITransform, _decorator } from 'cc';
import { Utils } from '../../../../../framework/Utils';
import { resLoader } from '../../../../../framework/loader/ResLoader';
import { BaseComponent } from '../../../../../framework/vc/BaseComponent';
import { FXJRes } from '../../../common/FXJRes';
import { CardUtil } from '../../../util/CardUtil';
const { ccclass, property } = _decorator;

@ccclass('Card')
export class Card extends BaseComponent {

    @property(UITransform)
    public card: UITransform | null = null;

    @property(Sprite)
    public bgImg: Sprite;

    @property(Sprite)
    public cardImg: Sprite | null = null;   

    public setCardByte(byte: number) {
        let cardStr = CardUtil.getCardValue(byte);
        let frameName = Utils.string_format(FXJRes.Prefab_Card.plistKey, cardStr)
        resLoader.load(FXJRes.Prefab_Card.bundle, FXJRes.Prefab_Card.path, SpriteAtlas, (err: Error | null, atlas) => {
            if (err) {
                return
            }
            let spriteFrame = atlas.getSpriteFrame(frameName)
            if (spriteFrame) {
                this.cardImg.spriteFrame = spriteFrame;
            }
        })
    }
}